.colaborador{
    width: 280px;
    margin-bottom: 24px;
}

.colaborador .Encabezado{
    background-color: #F0F0F0;
    border-radius: 10px 10px 0px 0px ;
}

.colaborador img{
    width: 100px;
    border-radius: 50%;
    position: relative;
    bottom: -50px;
}

.colaborador .info{
    background-color: #FFFFFF;
    box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.08);
    border-radius: 0px 0px 10px 10px;
    padding-top: 90px;
    padding-bottom: 40px;
    font-family: 'Montserrat', sans-serif;
    min-height: 100px;

}

.colaborador .info h4{
    color: #6278F7;
    font-size: 18px;
    line-height: 22px;
    font-weight: bold;
    margin-bottom: 8px;
}

.colaborador .info h5{
    font-size: 18px;
    line-height: 22px;
    color: #212121;
    padding: 0 24px;
    font-weight: 400;
    margin: 0;
}

