.Formulario{
    display: flex;
    justify-content: center;
    margin: 80px 0;
}

.Formulario form{
    max-width: 80%;
    background-color: #f7f7f7;
    box-shadow: 7px 7px 15px rgba(0, 0, 0, 15);
    border-radius: 20px;
    padding: 8px 100px;
}

.Formulario h2{
    font-size: 32px;
    font-weight: 400px;
    color: #212121;
}